.header-container {
  height: 60px;
  position: relative;
}

.hamburger {
  z-index: 1;
  width: 40px;
  height: inherit;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  padding: .8em 0;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.hamburger:hover > div {
  background: #f0f0f0;
}

.hamburger > div {
  background: #fff;
  border-radius: 3px;
  flex: none;
  width: 100%;
  height: 3px;
  transition: all .4s;
  position: relative;
}

.hamburger > div:before, .hamburger > div:after {
  content: "";
  z-index: 1;
  background: inherit;
  border-radius: 3px;
  width: 100%;
  height: 100%;
  position: absolute;
  top: -.7em;
}

.hamburger > div:after {
  top: .7em;
}

.hamburger.open {
  margin-right: 1em;
  position: fixed;
}

.hamburger.open:hover > div {
  transform: rotate(225deg);
}

.hamburger.open > div {
  transform: rotate(135deg);
}

.hamburger.open > div:before, .hamburger.open > div:after {
  top: 0;
  transform: rotate(90deg);
}

.nav {
  background-color: #299dcfe6;
  width: auto;
  height: 100vh;
  padding: 2em;
  transition: transform .3s cubic-bezier(0, .1, 0, .8);
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.nav-item {
  padding: 1em 0;
}

.nav-item_link {
  text-transform: uppercase;
  color: #fff;
  font-family: Montserrat, sans-serif;
  font-size: 1.1rem;
  font-weight: 500;
  text-decoration: none;
}

.nav-item_link:hover {
  color: #b3e7fd;
}

.nav.visible {
  transform: translateX(0);
}

.main-nav, .secondary-nav {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.main-nav {
  margin-bottom: 2em;
}

@media (width >= 800px) {
  .nav {
    background-color: #0000;
    justify-content: space-between;
    height: auto;
    padding: 1em 0;
    transition: none;
    position: static;
    transform: translateX(0);
  }

  .nav-item + .nav-item {
    margin-left: 1em;
  }

  .main-nav {
    margin-bottom: auto;
  }

  .header-container {
    height: auto;
    position: static;
  }

  .hamburger {
    display: none;
  }
}

.organizers-section .organizers__profiles.row {
  justify-content: space-around;
  display: flex;
}

.organizers-section .profile {
  cursor: pointer;
  text-align: center;
  margin: 0;
}

.organizers-section .profile .profile__img {
  border-radius: 50%;
  width: 50%;
  display: none;
}

.organizers-section .profile .profile__name {
  letter-spacing: .2em;
  text-transform: uppercase;
  text-align: center;
  padding: .8em .3em;
  font-size: 1.2rem;
  font-weight: 700;
}

.organizers-section .profile.profile--selected .profile__name {
  color: #00f;
}

@media (width >= 600px) {
  .organizers-section .row {
    display: flex;
  }

  .organizers-section .profile, .organizers-section .profile .profile__img {
    width: 100%;
    display: block;
  }

  .organizers-section .profile.profile--selected .profile__img {
    border: 4px solid #00f;
  }

  .organizers-section .profile + .profile {
    margin-left: 1.8em;
  }

  .organizers-section .organizer__about {
    justify-content: space-between;
    padding-top: 2em;
  }

  .organizers-section .organizer__about .organizer__about--photo {
    align-self: flex-start;
    width: 50%;
  }

  .organizers-section .organizer__about .about__text {
    width: 45%;
  }

  .organizers-section .organizer__about .about__text #about__name {
    margin: 0;
  }
}

@media (width >= 800px) {
  .organizers-section .profile .profile__img {
    width: 80%;
    margin: 0 auto;
  }
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #fff;
  background-color: #0198d8e7;
  max-width: 1000px;
  margin: auto;
  font-family: Roboto, sans-serif;
}

h1, h2, h3 {
  font-family: Montserrat, sans-serif;
}

p {
  line-height: 1.6;
}

img {
  width: 100%;
}

.container {
  width: 95%;
  max-width: 1000px;
  margin: 0 auto;
}

@media (width >= 800px) {
  .row {
    display: flex;
  }
}
